<template>
  <div class="router-tab-iframe-fake" />
</template>

<script>
// Iframe 
export const iframeMeta = {
  key: route => `iframe-${route.params.src}`,
  title: route => route.params.title,
  icon: route => route.params.icon
}

// Iframe 
export default {
  name: 'Iframe',
  inject: ['$tabs'],
  meta: iframeMeta, // Nuxt 

  props: {
    src: String,
    title: String,
    icon: String
  },

  computed: {
    /**
     *  XSS 
     * 1. `javascript:` `javascript:alert(1)`
     * 2. `data:` Base64 : `'data:text/html;base64,' + window.btoa('<script>alert(1)<\/script>')`
     */
    url() {
      let src = decodeURIComponent(this.src)

      if (/^(javascript|data):/i.test(src)) {
        return 'about:blank'
      }

      return src
    }
  },

  async mounted() {
    let { url, $tabs } = this
    let { iframes } = $tabs

    if (!iframes.includes(url)) {
      iframes.push(url)
    }

    $tabs.currentIframe = url

    await this.$nextTick()
    this.$tabs.iframeMounted(url)
  },

  activated() {
    this.$tabs.currentIframe = this.url
  },

  deactivated() {
    this.$tabs.currentIframe = null
  },

  //  iframe
  destroyed() {
    let { url } = this
    let { iframes } = this.$tabs
    let index = iframes.indexOf(url)

    if (index > -1) {
      iframes.splice(index, 1)
    }
  }
}
</script>
